import * as types from './types'

export const tickClock = (isServer) => {
   console.log('tickClock--Action', isServer);
   
    return {
        type: types.TICK_CLOCK,
        light: !isServer,
        ts: Date.now()
    }
}
export const startClock = () => {
    return {
        type: types.START_CLOCK
    }
}